html {
  height: 100%;
}

body {
  margin: 0;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  font-family: "Arial", "Helvetica", sans-serif;
  background: var(--white-color);
  color: var(--black-color);
  min-width: 320px;
  overflow-x: hidden;
}

// img {
//   max-width: 100%;
//   height: auto;
// }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: none;
  clip: rect(0 0 0 0);
}

.sticky-footer {
  flex-grow: 1;
}
